import { Component, h, Prop, EventEmitter, Event } from '@stencil/core';
import { ICustomField } from '../../types/CustomField';
import { IListItem } from '../../types/ListItem';

@Component({
    tag: 'custom-fields',
    shadow: true,
    styleUrl: 'custom-fields.scss',
})
export class CustomFields {
    @Prop() public template: IListItem;
    @Prop() public customFields: ICustomField[];
    @Prop() public isLoading: boolean;

    @Event() updateFieldValue: EventEmitter;

    constructor() {}

    public render() {
        if (this.isLoading) {
            return <ga-loader />;
        } else if (!this.template) {
            return [];
        }

        if (!this.customFields.length) {
            return [
                <h4>Fields</h4>,
                <empty-state
                    text="This template has no fields"
                    icon="text_box"
                />,
            ];
        }

        return [
            <h4>Fields</h4>,
            this.customFields.map(field => (
                <limel-input-field
                    class="custom-field"
                    label={field.label}
                    value={field.value}
                    disabled={!field.is_editable}
                    onChange={event => this.onChangeField(event, field.id)}
                />
            )),
        ];
    }

    private onChangeField(event, id) {
        this.updateFieldValue.emit({ id: id, value: event.detail });
    }
}
