/* eslint-disable no-debugger */
import { Component, h, Prop } from '@stencil/core';
import { IListItem } from '../../types/ListItem';
import { ISession } from '../../types/Session';

@Component({
    tag: 'template-preview',
    shadow: true,
    styleUrl: 'template-preview.scss',
})
export class TemplatePreview {
    @Prop() public template: IListItem;
    @Prop() public isLoading: boolean;
    @Prop() public session: ISession;

    constructor() {
        this.getThumbUrl = this.getThumbUrl.bind(this);
    }

    private getThumbUrl(): string {
        const path: string = `${this.session.entity_id}/${this.template.value}`;

        return `getaccept/preview_proxy/${path}`;
    }

    public render() {
        if (!this.template || this.isLoading) {
            return [];
        }

        return (
            <div class="page-info-container">
                <img class="page-thumb" src={this.template.thumbUrl} />
            </div>
        );
    }
}
