import { Component, h, Event, Prop, EventEmitter } from '@stencil/core';
import { IListItem } from 'src/types/ListItem';

@Component({
    tag: 'lime-document-list',
    shadow: true,
    styleUrl: 'lime-document-list.scss',
})
export class LimeDocumentList {
    @Prop() public documents = [];
    @Prop() public selectedLimeDocument: IListItem;
    @Prop() public isLoading: boolean;

    @Event() setLimeDocument: EventEmitter;

    constructor() {
        this.selectDocument = this.selectDocument.bind(this);
    }

    public render() {
        console.log(this.documents);
        if (this.isLoading) {
            return <ga-loader />;
        } else if (!this.documents.length) {
            return <empty-state text="No documents were found!" />;
        }

        return (
            <limel-list
                class="accordion-content"
                items={this.documents}
                type="selectable"
                onChange={this.selectDocument}
            />
        );
    }

    private selectDocument(event) {
        this.setLimeDocument.emit(event.detail);
    }
}
